import React from 'react'
import image from './image/corrida-nike.gif'
import styled from 'styled-components'
import { createGlobalStyle } from 'styled-components'

const imgRun = image

const GlobalStyle = createGlobalStyle`
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

:root{

  font-size: 62.5%;
  /* 1rem = 10px por (10*100)/16 = 62.5 */

}
`

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background: url(https://static.wixstatic.com/media/11062b_2b9fb27feaaa44f6a6669d0eab0153d0~mv2.jpeg/v1/fill/w_740,h_484,al_c,q_90/11062b_2b9fb27feaaa44f6a6669d0eab0153d0~mv2.webp)
    no-repeat center;
  background-size: contain;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: 'A B';
`

const ContainerFigure = styled.figure`
  grid-area: A;

  display: flex;
  justify-content: center;
  align-items: center;

  padding-left: 20rem;
  padding-top: 10rem;

  img {
    width: 22.5rem;
  }
`

const ContainerTrigger = styled.div`
  grid-area: B;
  display: flex;
  align-items: center;
  padding-top: 5rem;

  .contents {
    width: 48.6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    gap: 7.5rem;

    h1 {
      font-size: 4rem;
    }

    h2 {
      font-size: 3rem;
    }

    div {
      width: 37rem;
      height: 5rem;

      display: flex;
      justify-content: space-between;

      button {
        width: 11rem;
        font-size: 2rem;
        font-weight: 700;
        border-radius: 0.5rem;
      }
    }
  }
`

export default class Contador extends React.Component {
  state = {
    number: 0
  }

  add = () => {
    const functionTrigger = setInterval(() => {
      this.setState({ number: this.state.number + 1 })
    }, 1000)

    this.stop = () => {
      clearInterval(functionTrigger)
    }
  }

  reset = () => {
    this.setState({
      number: 0
    })
  }

  componentDidMount() {
    document.title = 'stopwatch'

    setTimeout(() => {
      document.title = this.state.number
    }, 2500)
  }

  componentDidUpdate() {
    document.title = this.state.number
  }

  render() {
    return (
      <div>
        <Container>
          <GlobalStyle />
          <ContainerFigure>
            <img
              src={`${imgRun}`}
              alt="Homem negro praticando corrida ao ar livre"
            />
          </ContainerFigure>
          <ContainerTrigger>
            <div className="contents">
              <h1>Welcome to stopwatch</h1>

              <h2>{this.state.number}</h2>

              <div>
                <button onClick={this.add}>PLAY</button>
                <button onClick={this.stop}>STOP</button>
                <button onClick={this.reset}>RESET</button>
              </div>
            </div>
          </ContainerTrigger>
        </Container>
      </div>
    )
  }
}
